'use client';

import Image from 'next/image';
import { useRouter } from 'next/navigation';
import { useState } from 'react';

interface Product {
  id: string;
  name: string;
  description: string;
  category: string;

  image?: string;
  tags?: string[];
  benefits?: string[];
  ingredients?: string[];
  storageInfo?: string;
  shelfLife?: string;
}

interface ProductCardProps {
  product: Product;
  variant?: 'grid' | 'list';
}

const ProductCard = ({ product, variant = 'grid' }: ProductCardProps) => {
  const { name, description, category, image, id } = product;
  const router = useRouter();
  const [imageError, setImageError] = useState(false);
  
  const handleProductClick = () => {
    router.push(`/products/${id}`);
  };

  const handleImageError = () => {
    setImageError(true);
  };

  if (variant === 'list') {
    return (
      <div 
        onClick={handleProductClick}
        className="flex flex-col md:flex-row gap-4 p-4 bg-white rounded-lg shadow-md hover:shadow-lg transition-all duration-300 cursor-pointer transform hover:scale-[1.02]"
      >
        <div className="relative w-full md:w-40 aspect-square">
          <Image
            src={imageError ? '/products/placeholder.jpg' : (image || '/products/placeholder.jpg')}
            alt={name || category}
            fill
            className="rounded-lg object-cover transition-transform duration-300 hover:scale-110"
            sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
            priority
            onError={handleImageError}
          />
        </div>
        <div className="flex-1">
          <div className="flex justify-between items-start">
            <span className="inline-block px-2 py-1 text-sm bg-orange-100 text-orange-600 rounded-full">
              {category}
            </span>
          </div>
          <h3 className="mt-2 text-lg font-semibold text-gray-800">{name}</h3>
          <p className="mt-2 text-gray-600">{description}</p>
        </div>
      </div>
    );
  }

  return (
    <div 
      className="group bg-white rounded-lg shadow-md hover:shadow-xl transition-all duration-300 relative overflow-hidden cursor-pointer transform hover:scale-[1.02]"
      onClick={handleProductClick}
      role="button"
      tabIndex={0}
      onKeyPress={(e) => e.key === 'Enter' && handleProductClick()}
    >
      <div className="relative w-full aspect-square overflow-hidden">
        <Image
          src={imageError ? '/products/placeholder.jpg' : (image || '/products/placeholder.jpg')}
          alt={name || category}
          fill
          className="rounded-t-lg object-cover transform group-hover:scale-110 transition-transform duration-500"
          sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
          priority
          onError={handleImageError}
        />
        {/* Hover overlay with full description */}
        <div className="absolute inset-0 bg-gradient-to-t from-black/70 via-black/50 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300 flex flex-col justify-end p-4">
          <p className="text-white text-sm sm:text-base mb-2 transition-transform duration-300 translate-y-2 group-hover:translate-y-0">
            {description}
          </p>
        </div>
      </div>
      <div className="p-4">
        <div className="flex justify-between items-center mb-2">
          <span className="inline-block px-2 py-1 text-sm bg-orange-100 text-orange-600 rounded-full">
            {category}
          </span>
        </div>
        <h3 className="text-lg font-semibold text-gray-800 mb-2">{name}</h3>
        <p className="text-sm text-gray-600 line-clamp-2 group-hover:text-orange-600 transition-colors duration-300">
          {description}
        </p>
        
        {/* Additional details on hover */}
        <div className="mt-3 pt-3 border-t border-gray-100 transform opacity-0 max-h-0 group-hover:max-h-20 group-hover:opacity-100 transition-all duration-300">
          <div className="flex justify-between items-center">
            
          </div>
        </div>
      </div>
    </div>
  );
}

export default ProductCard;